.grid-layout {
  position: relative;
  background-color: #FFFFFF;
}

.grid-box {
  position: absolute;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #EBECF0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 100%;
}

.grid-box-title {
  padding: 3px;
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
}

.grid-box-title-value {
  font-size: 18px;
}

.grid-box-content {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  overflow-y: auto;
}

.grid-box-content-panel {
  padding: 0px 5px;
  z-index: 1;
}

.grid-edit-panel {
  position:absolute; 
  right: 0; 
  top: 0; 
  background-color: transparent;
  padding: 4px;
  z-index: 2;
}

.grid-edit-panel:hover {
  background-color: #FFFFFF;
}

.grid-draggable {
  cursor: move;
}

.grid-resizable {
  width: 12px;
  height: 12px;
  position:absolute; 
  right: 0; 
  bottom: 0; 
  cursor: se-resize; 
}

.grid-box-file-button-group {
  float: right;
  display: none;
}

.grid-box-title:hover .grid-box-file-button-group {
  float: right;
  display: block;
  transition: 0.3s;
}

.grid-box-icon {
  cursor: pointer;
}


.tab-header {
  list-style-type: none;
  border-bottom: 1px solid #ccc;
  padding: 0px;
  margin: 0px;
}

.tab-header-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 8px 16px;
  min-width: 60px;
  cursor: pointer;
}

.tab-header-active {
  background-color: white;
  border: 1px solid #ccc;
  border-bottom: 3px solid #0065FF;
}

.tab-content {
  padding-top: 10px;
}
.workspace-name {
  display: block;
  color: #0065FF;
  padding: 7px 18px;
  font-size: 22px;
  font-weight: bold;
  float: left;
  margin-right: 6px;
}

.workspace-nav-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  float: left;
}

.workspace-nav-menu li {
  display: inline-block;
  list-style: none;
  font-size: 16px; 
}

.workspace-nav-menu-text {
  margin-left: 6px;
}

.workspace-nav-menu li a {
  display: block;
  color: #8993A4;
  text-decoration: none;
  padding: 9px 14px;   
}

.workspace-nav-menu li a:hover {
  border-bottom: 3px solid #0065FF;
  background-color: #C1C7D0;
}

.menu-item-active a {
  border-bottom: 3px solid #0065FF;
  background-color: #091E42;
  color: #FFFFFF !important;
}

.workspace-content {
  position: fixed;
  top: 40px;
  left: 0px;
  right: 0;
  bottom: 0;
  background-color: #e9ebee;
  overflow-x: hidden;
  overflow-y: auto; 
}

.workspace-account-menu {
  float: right;
}

.workspace-account-button a {
  display: block;
  color: #8993A4;
  padding: 9px 14px;
  text-decoration: none;
}

.workspace-account-button a:hover {
  border-bottom: 3px solid #0065FF;
  background-color: #C1C7D0;
}

.workspace-logout-button {
  color: #8993A4;
  padding: 9px 14px;
  text-decoration: none;
  cursor: pointer;
}
